<template>
  <v-card height="100%"  style="height: 300px;">
    <v-card-title>
      <!--  or    -->
      {{data.date}}
      {{data.is}}
    </v-card-title>
    <v-card-text class="scroll" > 
      <!--      or   v-for  -->
      <v-layout row>
      <v-subheader></v-subheader>
      <v-subheader></v-subheader>
      <v-subheader></v-subheader>
      <v-subheader></v-subheader>
      <!-- <v-rating v-model="rating"></v-rating> -->
      </v-layout>
      <div v-for="item in items">
        <AccountComp v-bind:data="{category:item.category, name:item.name, price:item.price, score:item.score}"></AccountComp>
      </div>

    </v-card-text>
    <v-card-actions>
      <v-layout row justify-center>
        <v-flex xs10>
          <v-btn slot="activator" color="orange" dark v-on:click="addbtn"></v-btn>
            
          <v-dialog v-model="dialog" persistent max-width="600px">
            <v-card>
              <v-card-title>
                <span class="headline"> </span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                    <v-flex xs12 class="addData">
                      : {{data.date}}
                      <!-- <v-text-field label="Legal first name*" required></v-text-field> -->
                    </v-flex>
                    <v-flex xs12 class="addData">
                      <br>
                      : {{data.is}}
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="" required v-model="addPrice"></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="" required v-model="addName"></v-text-field>
                    </v-flex> 
                    <v-flex xs12>
                      <span v-if="data.is==''">
                      <v-select
                        :items="['', '', '', '', '', '']"
                        label=""
                        required
                        v-model="addCategory"
                      ></v-select>
                      </span>
                      <span v-else>
                        <v-select
                        :items="['', '/', '', '/', '/', '', '/', '', '', '/', '', '']"
                        label=""
                        required
                        v-model="addCategory"
                        ></v-select>
                      </span>
                    </v-flex>
                    <v-flex xs12>
                      <v-rating v-model="addRate"></v-rating>
                    </v-flex>
                  </v-layout>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
                <v-btn color="blue darken-1" flat @click="dialog = false" v-on:click="add">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-card-actions>
      
  </v-card>
</template>

<script>
import AccountComp from "../components/AccountComp";


  export default{
    name : 'AccountShow',
    props:["data"],
    methods:{
      add:function(){
        this.items.push({
          date: this.data.date,
          is: this.data.is,
          price:this.addPrice,
          name:this.addName,
          category: this.addCategory,
          score:this.addRate
        })
      },
      addbtn:function(){
        if(this.data.date==null){
          alert('  ');
          this.dialog=false;
        }
        else this.dialog=true;
      }

    },
    data:function(){
      return{
        addRate:null,
        addName:null,
        addCategory:null,
        addPrice:null,
        dialog: false,
        items:[{}]
      }
    },
    components:{
      AccountComp
    }
  }
</script>

<style>
.addData{
  font-size: 18px
}
</style>



